<template>
  <div class="works_page" id="works_page">
    <div class="title_part" id="title_part">
      <div class="title" id="title">{{ global.lang_pack.works }}</div>
      <div class="title_line" id="title_line"></div>
    </div>

    <div class="works_list_part" id="works_list_part">
      <div class="works_row" id="works_row">
        <WorksCover
          v-if="config_list[0]"
          :id="id_list[0]"
          img_width="500rem"
          img_height="639rem"
          :cover_src="'/static/works/'+id_list[0]+'/cover.png'"
          :cover_title="config_list[0].cover_title"
          :tag_list="config_list[0].tag_list"
        ></WorksCover>

        <WorksCover
          v-if="config_list[1]"
          :id="id_list[1]"
          img_width="500rem"
          img_height="350rem"
          style="height:450rem;"
          :cover_src="'/static/works/'+id_list[1]+'/cover.png'"
          :cover_title="config_list[1].cover_title"
          :tag_list="config_list[1].tag_list"
        ></WorksCover>

        <WorksCover
          v-if="config_list[2]"
          :id="id_list[2]"
          img_width="500rem"
          img_height="639rem"
          :cover_src="'/static/works/'+id_list[2]+'/cover.png'"
          :cover_title="config_list[2].cover_title"
          :tag_list="config_list[2].tag_list"
        ></WorksCover>
      </div>

        <div class="works_row" id="works_row">
        <WorksCover
          v-if="config_list[3]"
          :id="id_list[3]"
          img_width="500rem"
          img_height="350rem"
            style="height:450rem;"
          :cover_src="'/static/works/'+id_list[3]+'/cover.png'"
          :cover_title="config_list[3].cover_title"
          :tag_list="config_list[3].tag_list"
        ></WorksCover>

        <WorksCover
          v-if="config_list[4]"
          :id="id_list[4]"
          img_width="500rem"
          img_height="639rem"
          style="margin-top: -290rem;"
          :cover_src="'/static/works/'+id_list[4]+'/cover.png'"
          :cover_title="config_list[4].cover_title"
          :tag_list="config_list[4].tag_list"
        ></WorksCover>

        <WorksCover
          v-if="config_list[5]"
          :id="id_list[5]"
          img_width="500rem"
          img_height="350rem"
            style="height:450rem;"
          :cover_src="'/static/works/'+id_list[5]+'/cover.png'"
          :cover_title="config_list[5].cover_title"
          :tag_list="config_list[5].tag_list"
        ></WorksCover>
      </div>

    </div>
    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
div {
  font-family: MyriadVariableConcept-Roman;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.works_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.title_part {
  margin-top: 100rem;
  margin-left: 300rem;
}

div.title {
  color: #251714;
  font-family: FuturaLT-ExtraBold;
  font-size: 100rem;
  text-transform: uppercase;
}

div.title_line {
  background: #efcd24;
  width: 450rem;
  height: 8rem;
  left: 625rem;
}

div.works_list_part {
  margin-top: 100rem;
  margin-left: 100rem;
  margin-right: 150rem;
}

div.works_row{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 50rem;
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import WorksCover from "../../components/works/cover/Cover";

//export default
export default {
  name: "Works",

  data() {
    return {
      global: global,
      config_list: [],
      id_list: ["ATLANTIS", "MGM_HOTEL", "BASIS_INTERNATIONAL","DYT_HOTEL","GRAND_HYATT","CENTRAL_YOMA"],
    };
  },
  components: {
    Menu,
    WorksCover,
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.get_covers();
  },
  destroyed() {},
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    get_covers() {
      let that = this;
      that.id_list.forEach((id) => {
        that.config_list.push({
          cover_title: require("../../../static/works/" +
            id +
            "/config.js").id.replace("_", " "),
          tag_list: require("../../../static/works/" + id + "/config.js")[
            "works_content_" + global.lang
          ].tag_list,
        });
      });
    },
  },
};
</script>



